<template>
  <main class="Stat Wrapper">
    <BaseNavbar
      v-if="isAuth && canUserSeeAllStat"
      className="Stat-Nav"
      :navItems="navItems"
      @set-nav-value="selectActiveNavProp"
    />
    <div v-if="!isAuth && canUserSeeAllStat" class="Stat-Title">
      All statistics
    </div>
    <div v-if="isAuth && !canUserSeeAllStat" class="Stat-Title">
      My posts
    </div>
    <div v-if="isAuth || canUserSeeAllStat" class="Stat-Content">
      <div class="Stat-Row">
        <div class="Stat-Prop">
          Posts:
        </div>
        <div class="Stat-Value">
          {{ postsCount }}
        </div>
      </div>
      <div class="Stat-Row">
        <div class="Stat-Prop">
          Likes:
        </div>
        <div class="Stat-Value">
          {{ likeCount }}
        </div>
      </div>
      <div class="Stat-Row">
        <div class="Stat-Prop">
          Dislikes:
        </div>
        <div class="Stat-Value">
          {{ dislikeCount }}
        </div>
      </div>
      <div class="Stat-Row">
        <div class="Stat-Prop">
          Views
        </div>
        <div class="Stat-Value">
          {{ viewCount }}
        </div>
      </div>
      <div class="Stat-Row">
        <div class="Stat-Prop">
          First post:
        </div>
        <div class="Stat-Value">
          {{ firstPublication | formatRelTime }}
        </div>
      </div>
    </div>
    <div
      v-if="!isAuth && !settings.STATISTICS_IS_PUBLIC"
      class="ServerInfo Stat-Info"
    >
      Sorry, the statistics are not available.
    </div>
  </main>
</template>

<script>
const BaseNavbar = () =>
  import(/* webpackChunkName: "baseNavbar" */ "@/components/BaseNavbar.vue");
import axios from "axios";
import { SERVER_URL } from "./../env";
import { mapGetters } from "vuex";

export default {
  name: "Stat",

  components: {
    BaseNavbar
  },

  data() {
    return {
      navItems: [
        {
            name: "All over the blog",
            value: "all"
        },
        {
          name: "My",
          value: "my"
        }
      ],
      activeNavProp: 0,
      isLoading: true,
      isErrored: false,
      postsCount: 0,
      likeCount: 0,
      dislikeCount: 0,
      viewCount: 0,
      firstPublication: null,
      errors: []
    };
  },

  computed: {
    ...mapGetters(["isAuth", "settings", "blogInfo", "user"]),

    canUserSeeAllStat() {
        return this.user.moderation || this.settings.STATISTICS_IS_PUBLIC;
    }
  },

  watch: {
    activeNavProp() {
      this.getStats();
    }
  },

  methods: {
    selectActiveNavProp(value) {
      this.activeNavProp = value;
    },

    getStats() {
      let param;
      if (this.isAuth && this.canUserSeeAllStat) {
        param = this.navItems[this.activeNavProp].value;
      } else if (this.isAuth) {
          param = "my";
      } else if (this.canUserSeeAllStat) {
          param = "all";
      } else {
          return;
      }


      axios
        .get(`${SERVER_URL}/api/statistics/${param}`)
        .then(res => {
          if (res.status === 401) this.settings.STATISTICS_IS_PUBLIC = true;
          else {
            this.postsCount = res.data.postsCount;
            this.likeCount = res.data.likeCount;
            this.dislikeCount = res.data.dislikeCount;
            this.viewCount = res.data.viewCount;
            this.firstPublication = res.data.firstPublication;
          }
        })
        .catch(e => {
          this.errors.push(e);
          this.isErrored = true;
        })
        .finally(() => (this.isLoading = false));
    }
  },

  mounted() {
    this.getStats();
  },

  metaInfo() {
    return {
      title: this.blogInfo
        ? `Statistics | ${this.blogInfo.title} - ${this.blogInfo.subtitle}`
        : "Statistics"
    };
  }
};
</script>

