  display: inline-block;

  &-Nav {
    justify-content: flex-start;
  }

  &-Title {
    margin-top: 25px;
    margin-bottom: 25px;
    font-size: 1.6rem;
    font-weight: 700;
  }

  &-Content {
    margin-top: 25px;
  }

  &-Row {
    display: flex;
    margin-bottom: 10px;
    font-size: 1.4rem;
  }

  &-Prop {
    width: 150px;
  }

  &-Info {
    margin-top: 45px;
  }
}
</style>
